body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-attachment: scroll;
  height: 100vh;
  background-size: cover;
  /* background-image: linear-gradient(to bottom, rgba(255,255,255,0.8) 0%,rgba(255, 255, 255, 0.8) 100%), url('./assets/img/staff.jpg'); */
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.8)),to(rgba(180, 177, 177, 0.8))), url(/static/media/staff.fc0bf8ad.jpg);
  background-image: linear-gradient(to bottom, rgba(255,255,255,0.8) 0%,rgba(180, 177, 177, 0.8) 100%), url(/static/media/staff.fc0bf8ad.jpg);
  background-color: #D5D4D3;
}

.App_H1 {
  font-family: 'Cabin', sans-serif;
  font-size: 60px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

